import BannerPlugin from './App.vue'

export const inBrowser = typeof window !== 'undefined'

BannerPlugin.install = function(Vue) {
  Vue.component(BannerPlugin.name, BannerPlugin)
}

export default BannerPlugin

if (inBrowser && window.Vue) {
  window.Vue.use(BannerPlugin)
}


